import React, { useState, useCallback } from 'react';
import Game from './components/Game';
import PhysicsTest from './components/PhysicsTest'; // Add physics test
import './index.css';

function App() {
  const [gameState, setGameState] = useState('start'); // 'start', 'playing', 'gameOver'
  const [score, setScore] = useState(0);
  const [highScore, setHighScore] = useState(
    parseInt(localStorage.getItem('dinoHighScore') || '0')
  );
  const [obstaclesAvoided, setObstaclesAvoided] = useState(0);

  const startGame = useCallback(() => {
    setGameState('playing');
    setScore(0);
    setObstaclesAvoided(0);
  }, []);

  const endGame = useCallback((finalScore, finalObstacles) => {
    setGameState('gameOver');
    if (finalScore > highScore) {
      setHighScore(finalScore);
      localStorage.setItem('dinoHighScore', finalScore.toString());
    }
  }, [highScore]);

  const resetGame = useCallback(() => {
    setGameState('start');
  }, []);

  return (
    <div className="App">
      {gameState === 'start' && (
        <div className="start-screen">
          <h1>🦕 DINO RUNNER 3D</h1>
          <p>
            Navigate the prehistoric landscape!<br />
            <strong>SPACE</strong> to jump over obstacles<br />
            <strong>DOWN ARROW</strong> to duck under birds<br />
            Avoid rocks, pits, and flying creatures!
          </p>
          <button onClick={startGame}>START ADVENTURE</button>
        </div>
      )}

      {gameState === 'playing' && (
        <>
          <div className="game-ui">
            <div>Distance: {score}m</div>
            <div>Obstacles Avoided: {obstaclesAvoided}</div>
            <div>High Score: {highScore}m</div>
          </div>
          <div className="instructions">
            SPACE: Jump | DOWN ARROW: Duck
          </div>
        </>
      )}

      {gameState === 'gameOver' && (
        <div className="game-over">
          <h2>🦴 GAME OVER!</h2>
          <p>Distance: {score}m</p>
          <p>Obstacles Avoided: {obstaclesAvoided}</p>
          <p>High Score: {highScore}m</p>
          {score === highScore && score > 0 && (
            <p style={{ color: '#FFD700', fontSize: '18px' }}>🏆 NEW HIGH SCORE!</p>
          )}
          <button onClick={resetGame}>TRY AGAIN</button>
        </div>
      )}

      <Game
        gameState={gameState}
        onScoreUpdate={setScore}
        onObstacleAvoided={() => setObstaclesAvoided(prev => prev + 1)}
        onGameOver={endGame}
      />
    </div>
  );
}

export default App;
