import React, { useRef, useMemo } from 'react';
import { useFrame } from '@react-three/fiber';
import { usePlane, useBox } from '@react-three/cannon';
import * as THREE from 'three';

function Terrain({ gameState, gameSpeed, distance }) {
  const groupRef = useRef();
  const terrainSegments = useRef([]);
  const segmentLength = 50;
  const numberOfSegments = 12; // Increased from 8 to 12 for better coverage

  // Ground physics
  const [groundRef] = usePlane(() => ({
    rotation: [-Math.PI / 2, 0, 0],
    position: [0, 0, 0],
    type: 'Static',
  }));

  // Generate terrain segments - regenerate when game restarts
  const segments = useMemo(() => {
    const segs = [];
    for (let i = 0; i < numberOfSegments; i++) {
      segs.push(createTerrainSegment(i));
    }
    // Add extra segments behind and around the player for immediate visibility
    for (let i = 0; i < 6; i++) {
      const extraSegment = createTerrainSegment(-i - 1);
      extraSegment.startZ = (-i - 1) * segmentLength - 200;
      segs.push(extraSegment);
    }
    return segs;
  }, [gameState]); // Reset terrain when game state changes

  function createTerrainSegment(index) {
    const startZ = index * segmentLength - 200; // Position segments starting from behind the player
    const hills = [];
    const rocks = [];
    const roadSideHills = [];

    // Create immediate hills around starting position (z=0) for instant visibility
    if (index <= 1) { // Only for the first couple segments
      // Generate hills very close to starting position - LEFT SIDE
      for (let i = 0; i < 4; i++) {
        const x = -15 - i * 6;
        const z = -50 + i * 25; // Spread hills from z=-50 to z=25, covering starting area
        const height = 4 + Math.random() * 6;
        const width = 6 + Math.random() * 4;
        
        hills.push({
          position: [x, height / 2, z],
          scale: [width, height, width],
          color: '#3a5f3a',
        });
      }

      // Generate hills very close to starting position - RIGHT SIDE
      for (let i = 0; i < 4; i++) {
        const x = 15 + i * 6;
        const z = -50 + i * 25; // Spread hills from z=-50 to z=25, covering starting area
        const height = 4 + Math.random() * 6;
        const width = 6 + Math.random() * 4;
        
        hills.push({
          position: [x, height / 2, z],
          scale: [width, height, width],
          color: '#3a5f3a',
        });
      }
    }

    // Generate regular terrain hills for other segments
    // LEFT SIDE of road
    for (let i = 0; i < 4; i++) {
      const x = -15 - Math.random() * 20;
      const z = startZ + Math.random() * segmentLength;
      const height = 3 + Math.random() * 8;
      const width = 4 + Math.random() * 8;
      
      // Ensure hill edge doesn't reach road
      const minX = -6 - width / 2;
      const safeX = Math.min(x, minX);
      
      hills.push({
        position: [safeX, height / 2, z],
        scale: [width, height, width],
        color: '#3a5f3a',
      });
    }

    // RIGHT SIDE of road  
    for (let i = 0; i < 4; i++) {
      const x = 15 + Math.random() * 20;
      const z = startZ + Math.random() * segmentLength;
      const height = 3 + Math.random() * 8;
      const width = 4 + Math.random() * 8;
      
      // Ensure hill edge doesn't reach road
      const minX = 6 + width / 2;
      const safeX = Math.max(x, minX);
      
      hills.push({
        position: [safeX, height / 2, z],
        scale: [width, height, width],
        color: '#3a5f3a',
      });
    }

    // Add some distant background hills for depth
    for (let i = 0; i < 4; i++) {
      const side = i < 2 ? -1 : 1; // 2 on each side
      const x = side * (60 + Math.random() * 80); // Very far from road
      const z = startZ + Math.random() * segmentLength;
      const height = 15 + Math.random() * 25; // Tall background hills
      const width = 20 + Math.random() * 30; // Large background hills (safe since they're far away)
      
      hills.push({
        position: [x, height / 2, z],
        scale: [width, height, width],
        color: '#2d4a2d', // Darker for distance
      });
    }

    // Create road embankment/shoulder hills (closer to road but still safe)
    for (let i = 0; i < 6; i++) {
      const side = i < 3 ? -1 : 1; // 3 on each side
      const baseX = side * (10 + Math.random() * 6); // Closer base position
      const height = 1.5 + Math.random() * 3; // Lower hills near road
      const width = 3 + Math.random() * 4; // Smaller width to be safe
      const z = startZ + (Math.random() * segmentLength);
      
      // Calculate safe position ensuring no road overlap
      const safeDistance = 6 + width / 2; // Road edge + hill radius
      const safeX = side > 0 ? Math.max(baseX, safeDistance) : Math.min(baseX, -safeDistance);
      
      roadSideHills.push({
        position: [safeX, height / 2, z],
        scale: [width, height, width],
        color: '#5a8a5a', // Slightly different shade
      });
    }

    // Scatter rocks across the hilly terrain (outside road area)
    for (let i = 0; i < 12; i++) {
      const side = Math.random() > 0.5 ? 1 : -1;
      const x = side * (15 + Math.random() * 35); // Start at ±15 to be well clear of road
      const z = startZ + Math.random() * segmentLength;
      const size = 0.5 + Math.random() * 1.5;
      
      rocks.push({
        position: [x, size / 2, z],
        scale: [size, size, size],
        color: '#666666',
      });
    }

    return { startZ, hills, rocks, roadSideHills };
  }

  useFrame(() => {
    if (gameState !== 'playing') return;

    // Update segment positions and regenerate if needed
    segments.forEach((segment, index) => {
      const segmentWorldZ = segment.startZ - distance; // Current world position
      
      // If segment is too far behind, move it to the front
      // Increased distance to prevent sudden vanishing near camera
      if (segmentWorldZ < -segmentLength * 4) { // Changed from 2 to 4 for longer visibility
        const lastSegmentZ = Math.max(...segments.map(s => s.startZ));
        segment.startZ = lastSegmentZ + segmentLength;
        
        // Regenerate content for variety
        Object.assign(segment, createTerrainSegment(0));
        segment.startZ = lastSegmentZ + segmentLength;
      }
    });
  });

  return (
    <group ref={groupRef}>
      {/* Invisible ground plane for physics */}
      <mesh ref={groundRef} receiveShadow visible={false}>
        <planeGeometry args={[1000, 1000]} />
        <meshLambertMaterial color="#8B4513" />
      </mesh>

      {/* Hilly base ground - natural terrain color */}
      <mesh receiveShadow position={[0, -0.1, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[1000, 1000]} />
        <meshLambertMaterial color="#7a5f47" />
      </mesh>

      {/* Road cut through the hills - elevated and clean */}
      <mesh receiveShadow position={[0, 0.02, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[8, 1000]} />
        <meshLambertMaterial color="#5a4a3a" />
      </mesh>

      {/* Road center line for better visibility */}
      <mesh receiveShadow position={[0, 0.03, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[0.3, 1000]} />
        <meshLambertMaterial color="#ffff80" />
      </mesh>

      {/* Road edges/shoulders */}
      <mesh receiveShadow position={[-4.2, 0.01, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[0.4, 1000]} />
        <meshLambertMaterial color="#8B7355" />
      </mesh>
      <mesh receiveShadow position={[4.2, 0.01, 0]} rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[0.4, 1000]} />
        <meshLambertMaterial color="#8B7355" />
      </mesh>

      {/* Render terrain segments - terrain moves backward as player runs forward */}
      {segments.map((segment, segmentIndex) => {
        const segmentZ = segment.startZ - distance; // This moves terrain backward
        
        return (
          <group key={segmentIndex} position={[0, 0, segmentZ]}>
            {/* Background hills */}
            {segment.hills.map((hill, hillIndex) => (
              <Hill
                key={`hill-${segmentIndex}-${hillIndex}`}
                position={hill.position}
                scale={hill.scale}
                color={hill.color}
              />
            ))}

            {/* Roadside hills */}
            {segment.roadSideHills?.map((hill, hillIndex) => (
              <Hill
                key={`roadside-hill-${segmentIndex}-${hillIndex}`}
                position={hill.position}
                scale={hill.scale}
                color={hill.color}
              />
            ))}
            
            {/* Decorative rocks */}
            {segment.rocks.map((rock, rockIndex) => (
              <Rock
                key={`rock-${segmentIndex}-${rockIndex}`}
                position={rock.position}
                scale={rock.scale}
                color={rock.color}
              />
            ))}
          </group>
        );
      })}
    </group>
  );
}

// Hill component
function Hill({ position, scale, color }) {
  return (
    <mesh position={position} scale={scale} receiveShadow>
      <coneGeometry args={[1, 1, 8]} />
      <meshStandardMaterial color={color} roughness={0.8} />
    </mesh>
  );
}

// Rock component
function Rock({ position, scale, color }) {
  return (
    <mesh position={position} scale={scale} castShadow receiveShadow>
      <dodecahedronGeometry args={[1]} />
      <meshLambertMaterial color={color} />
    </mesh>
  );
}

export default Terrain;
