import React, { useRef, useState, useMemo, useCallback } from 'react';
import { useFrame } from '@react-three/fiber';
import { useBox } from '@react-three/cannon';
import * as THREE from 'three';

function ObstacleManager({ gameState, gameSpeed, distance, onObstacleAvoided, onCollision }) {
  const [obstacles, setObstacles] = useState([]);
  const lastSpawnDistance = useRef(0);
  const obstacleIdCounter = useRef(0);
  const avoidedObstacles = useRef(new Set());

  const spawnDistance = useMemo(() => {
    // Decrease spawn distance as speed increases (more frequent obstacles)
    // INCREASED: 4-5x the previous value
    return Math.max(60, 120 - Math.floor(distance / 200) * 8); // was 15, 30
  }, [distance]);

  const spawnObstacle = useCallback(() => {
    const types = ['small_cactus', 'large_cactus', 'bird', 'gate'];
    const weights = [0.4, 0.3, 0.15, 0.15]; // More ground obstacles
    
    // Adjust difficulty over time
    const difficultyFactor = Math.min(distance / 1000, 1);
    
    let selectedType = 'small_cactus';
    const random = Math.random();
    let cumulative = 0;
    
    for (let i = 0; i < types.length; i++) {
      cumulative += weights[i];
      if (random < cumulative) {
        selectedType = types[i];
        break;
      }
    }

    const newObstacle = {
      id: obstacleIdCounter.current++,
      type: selectedType,
      position: [
        // Keep ground obstacles (cactus) in center, allow aerial obstacles some variation
        selectedType === 'bird' || selectedType === 'gate' ? (Math.random() - 0.5) * 2 : 0,
        selectedType === 'bird' || selectedType === 'gate' ? 2 + Math.random() * 0.5 : 0.5, // Aerial obstacles low enough to require ducking
        50 // Fixed spawn distance ahead, independent of current distance
      ],
      initialZ: 50, // Store initial position for consistent movement
      scale: selectedType === 'large_cactus' ? [2, 2, 1.5] : [1.5, 1.5, 1.5],
      active: true,
      spawnDistance: distance, // Track when it was spawned
    };

    return newObstacle;
  }, [distance]);

  useFrame(() => {
    if (gameState !== 'playing') return;

    // Spawn new obstacles
    if (distance - lastSpawnDistance.current > spawnDistance) {
      setObstacles(prev => [...prev, spawnObstacle()]);
      lastSpawnDistance.current = distance;
    }

    // Update obstacle positions and check for avoidance
    setObstacles(prev => 
      prev.map(obstacle => {
        // Calculate position relative to when it was spawned
        const relativeDistance = distance - obstacle.spawnDistance;
        const newZ = obstacle.initialZ - relativeDistance;
        
        // Check if obstacle was avoided (passed behind player)
        if (newZ < -5 && !avoidedObstacles.current.has(obstacle.id)) {
          avoidedObstacles.current.add(obstacle.id);
          onObstacleAvoided();
        }
        
        return {
          ...obstacle,
          position: [obstacle.position[0], obstacle.position[1], newZ],
          active: newZ > -100, // Keep active until far behind
        };
      }).filter(obstacle => obstacle.active)
    );
  });

  // Reset when game restarts
  React.useEffect(() => {
    if (gameState === 'playing') {
      setObstacles([]);
      lastSpawnDistance.current = 0;
      avoidedObstacles.current.clear();
    }
  }, [gameState]);

  return (
    <group>
      {obstacles.map(obstacle => (
        <Obstacle
          key={obstacle.id}
          obstacle={obstacle}
          onCollision={onCollision}
        />
      ))}
    </group>
  );
}

function Obstacle({ obstacle, onCollision }) {
  const meshRef = useRef();
  const animationRef = useRef(0);

  // Physics body with better collision detection
  const [ref, api] = useBox(() => ({
    position: obstacle.position,
    args: obstacle.scale,
    type: 'Kinematic', // Use Kinematic for moving obstacles
    mass: 0,
    isTrigger: false, // Solid collision
    userData: { type: 'obstacle', obstacleId: obstacle.id },
    collisionFilterGroup: 1,
    collisionFilterMask: 1,
    material: {
      friction: 0.1,
      restitution: 0.1,
    },
    onCollide: (e) => {
      console.log('Obstacle collision event:', {
        thisUserData: e.target?.userData,
        otherUserData: e.body?.userData
      });
      // More reliable collision detection - check for dinosaur
      if (e.body && e.body.userData && 
          (e.body.userData.type === 'dinosaur' || e.body.userData.isDinosaur)) {
        console.log('Hit obstacle!');
        onCollision();
      }
    },
  }));

  useFrame((state, delta) => {
    // Update physics body position using API for kinematic bodies
    api.position.set(obstacle.position[0], obstacle.position[1], obstacle.position[2]);

    // Sync visual mesh to obstacle position (not physics body position)
    if (meshRef.current) {
      meshRef.current.position.set(obstacle.position[0], obstacle.position[1], obstacle.position[2]);
      
      // Animate flying obstacles
      if (obstacle.type === 'bird') {
        animationRef.current += delta * 8;
        const flutter = Math.sin(animationRef.current) * 0.3;
        meshRef.current.position.y = obstacle.position[1] + flutter;
        meshRef.current.rotation.z = flutter * 0.2;
      }
    }
  });

  const renderObstacle = () => {
    switch (obstacle.type) {
      case 'small_cactus':
        return <SmallCactusObstacle scale={obstacle.scale} />;
      case 'large_cactus':
        return <LargeCactusObstacle scale={obstacle.scale} />;
      case 'bird':
        return <BirdObstacle scale={obstacle.scale} />;
      case 'gate':
        return <GateObstacle scale={obstacle.scale} />;
      default:
        return <SmallCactusObstacle scale={obstacle.scale} />;
    }
  };

  return (
    <group>
      {/* Physics body (invisible) */}
      <mesh ref={ref} visible={false}>
        <boxGeometry args={obstacle.scale} />
      </mesh>
      
      {/* Visual representation */}
      <group ref={meshRef}>
        {renderObstacle()}
      </group>
    </group>
  );
}

// Small Cactus Obstacle
function SmallCactusObstacle({ scale }) {
  return (
    <group scale={scale}>
      {/* Main trunk */}
      <mesh position={[0, 0.8, 0]} castShadow receiveShadow>
        <cylinderGeometry args={[0.3, 0.4, 1.6, 8]} />
        <meshStandardMaterial color="#228B22" roughness={0.8} />
      </mesh>
      
      {/* Small side arms */}
      <mesh position={[-0.5, 0.5, 0]} rotation={[0, 0, Math.PI / 3]} castShadow>
        <cylinderGeometry args={[0.15, 0.2, 0.6, 6]} />
        <meshStandardMaterial color="#32CD32" roughness={0.8} />
      </mesh>
      <mesh position={[0.4, 0.3, 0]} rotation={[0, 0, -Math.PI / 4]} castShadow>
        <cylinderGeometry args={[0.12, 0.18, 0.5, 6]} />
        <meshStandardMaterial color="#32CD32" roughness={0.8} />
      </mesh>

      {/* Spines/needles */}
      <mesh position={[0, 1.2, 0.3]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 0.8, 0.35]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 0.4, 0.38]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 1.0, -0.35]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 0.6, -0.32]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
    </group>
  );
}

// Large Cactus Obstacle
function LargeCactusObstacle({ scale }) {
  return (
    <group scale={scale}>
      {/* Main trunk - taller and thicker */}
      <mesh position={[0, 1.2, 0]} castShadow receiveShadow>
        <cylinderGeometry args={[0.4, 0.5, 2.4, 8]} />
        <meshStandardMaterial color="#228B22" roughness={0.8} />
      </mesh>
      
      {/* Large side arms */}
      <mesh position={[-0.8, 1.0, 0]} rotation={[0, 0, Math.PI / 2]} castShadow>
        <cylinderGeometry args={[0.2, 0.25, 1.2, 6]} />
        <meshStandardMaterial color="#32CD32" roughness={0.8} />
      </mesh>
      <mesh position={[0.7, 0.8, 0]} rotation={[0, 0, -Math.PI / 3]} castShadow>
        <cylinderGeometry args={[0.18, 0.22, 1.0, 6]} />
        <meshStandardMaterial color="#32CD32" roughness={0.8} />
      </mesh>
      
      {/* Upper arm extensions */}
      <mesh position={[-1.3, 1.0, 0]} rotation={[0, 0, 0]} castShadow>
        <cylinderGeometry args={[0.15, 0.18, 0.8, 6]} />
        <meshStandardMaterial color="#32CD32" roughness={0.8} />
      </mesh>
      
      {/* More spines for larger cactus */}
      <mesh position={[0, 2.0, 0.4]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 1.6, 0.42]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 1.2, 0.45]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 0.8, 0.42]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 0.4, 0.45]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      
      {/* Back spines */}
      <mesh position={[0, 1.8, -0.42]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 1.4, -0.40]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0, 1.0, -0.43]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      
      {/* Side arm spines */}
      <mesh position={[-1.3, 1.2, 0.18]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
      <mesh position={[0.7, 1.0, 0.20]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#8B0000" />
      </mesh>
    </group>
  );
}

function RockObstacle({ scale }) {
  return (
    <mesh castShadow receiveShadow scale={scale}>
      <dodecahedronGeometry args={[1]} />
      <meshLambertMaterial color="#444444" />
    </mesh>
  );
}

function PitObstacle({ scale }) {
  return (
    <group>
      <mesh position={[0, -0.2, 0]} scale={scale} receiveShadow>
        <boxGeometry args={[1, 1, 1]} />
        <meshLambertMaterial color="#1a1a1a" />
      </mesh>
      {/* Pit edges */}
      <mesh position={[scale[0]/2, 0, 0]} scale={[0.2, 0.5, scale[2]]}>
        <boxGeometry args={[1, 1, 1]} />
        <meshLambertMaterial color="#654321" />
      </mesh>
      <mesh position={[-scale[0]/2, 0, 0]} scale={[0.2, 0.5, scale[2]]}>
        <boxGeometry args={[1, 1, 1]} />
        <meshLambertMaterial color="#654321" />
      </mesh>
    </group>
  );
}

function BirdObstacle({ scale }) {
  return (
    <group scale={scale}>
      {/* Body */}
      <mesh>
        <sphereGeometry args={[0.6, 8, 6]} />
        <meshLambertMaterial color="#8B4513" />
      </mesh>
      
      {/* Wings */}
      <mesh position={[-0.8, 0, 0]} rotation={[0, 0, 0.3]}>
        <boxGeometry args={[0.8, 0.1, 1.2]} />
        <meshLambertMaterial color="#A0522D" />
      </mesh>
      <mesh position={[0.8, 0, 0]} rotation={[0, 0, -0.3]}>
        <boxGeometry args={[0.8, 0.1, 1.2]} />
        <meshLambertMaterial color="#A0522D" />
      </mesh>
      
      {/* Beak */}
      <mesh position={[0.7, 0.1, 0]}>
        <coneGeometry args={[0.1, 0.3, 6]} />
        <meshLambertMaterial color="#FFA500" />
      </mesh>
    </group>
  );
}

function GateObstacle({ scale }) {
  return (
    <group scale={scale}>
      {/* Left post */}
      <mesh position={[-2, 0, 0]} castShadow>
        <boxGeometry args={[0.3, 4, 0.3]} />
        <meshLambertMaterial color="#8B4513" />
      </mesh>
      
      {/* Right post */}
      <mesh position={[2, 0, 0]} castShadow>
        <boxGeometry args={[0.3, 4, 0.3]} />
        <meshLambertMaterial color="#8B4513" />
      </mesh>
      
      {/* Top bar */}
      <mesh position={[0, 1.5, 0]} castShadow>
        <boxGeometry args={[4.6, 0.3, 0.3]} />
        <meshLambertMaterial color="#8B4513" />
      </mesh>
    </group>
  );
}

export default ObstacleManager;
