import React, { useRef, useEffect, useState, useCallback } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Physics } from '@react-three/cannon';
import * as THREE from 'three';

import Dinosaur from './Dinosaur';
import Terrain from './Terrain';
import ObstacleManager from './ObstacleManager';
import Camera from './Camera';

function Game({ gameState, onScoreUpdate, onObstacleAvoided, onGameOver }) {
  const [gameSpeed, setGameSpeed] = useState(1);
  const [distance, setDistance] = useState(0);
  const gameRef = useRef({ isRunning: false, distance: 0, speed: 1 });

  // Game loop component
  const GameLoop = () => {
    useFrame((state, delta) => {
      if (gameState !== 'playing') return;

      // Update distance
      const newDistance = gameRef.current.distance + (gameRef.current.speed * delta * 50);
      gameRef.current.distance = newDistance;
      setDistance(newDistance);
      onScoreUpdate(Math.floor(newDistance));

      // Increase speed over time
      const newSpeed = 1 + Math.floor(newDistance / 500) * 0.2;
      if (newSpeed !== gameRef.current.speed) {
        gameRef.current.speed = newSpeed;
        setGameSpeed(newSpeed);
      }
    });

    return null;
  };

  // Reset game state when starting
  useEffect(() => {
    if (gameState === 'playing') {
      gameRef.current = { isRunning: true, distance: 0, speed: 1 };
      setDistance(0);
      setGameSpeed(1);
    } else {
      gameRef.current.isRunning = false;
    }
  }, [gameState]);

  const handleCollision = useCallback(() => {
    if (gameState === 'playing') {
      onGameOver(Math.floor(distance), 0); // We'll track obstacles separately
    }
  }, [gameState, distance, onGameOver]);

  return (
    <Canvas
      shadows
      camera={{ position: [0, 8, 12], fov: 60 }}
      style={{ height: '100vh', width: '100vw' }}
    >
      {/* Lighting */}
      <ambientLight intensity={0.6} />
      <directionalLight
        position={[50, 50, 25]}
        castShadow
        shadow-mapSize={[2048, 2048]}
        shadow-camera-left={-50}
        shadow-camera-right={50}
        shadow-camera-top={50}
        shadow-camera-bottom={-50}
        intensity={1.5}
      />
      
      {/* Simple background color instead of Sky */}
      <color attach="background" args={['#87CEEB']} />

      {/* Physics World - Rotation temporarily disabled for testing */}
      <Physics gravity={[0, -30, 0]} allowSleep={false}>
        <group>
          {/* Game Components */}
          <Dinosaur
            gameState={gameState}
            gameSpeed={gameSpeed}
            onCollision={handleCollision}
          />
          
          <Terrain
            gameState={gameState}
            gameSpeed={gameSpeed}
            distance={distance}
          />
          
          <ObstacleManager
            gameState={gameState}
            gameSpeed={gameSpeed}
            distance={distance}
            onObstacleAvoided={onObstacleAvoided}
            onCollision={handleCollision}
          />

          <Camera gameState={gameState} />
        </group>
      </Physics>

      {/* Game Loop */}
      <GameLoop />
    </Canvas>
  );
}

export default Game;
