import React, { useRef } from 'react';
import { useFrame, useThree } from '@react-three/fiber';
import * as THREE from 'three';

function Camera({ gameState }) {
  const { camera } = useThree();
  const targetPosition = useRef(new THREE.Vector3(0, 8, 12));
  const currentPosition = useRef(new THREE.Vector3(0, 8, 12));
  const targetLookAt = useRef(new THREE.Vector3(0, 2, 0));
  const currentLookAt = useRef(new THREE.Vector3(0, 2, 0));

  useFrame((state, delta) => {
    if (gameState !== 'playing') {
      // Menu camera position
      targetPosition.current.set(0, 10, 15);
      targetLookAt.current.set(0, 0, 0);
    } else {
      // Game camera position - rotated 180 degrees to face opposite direction
      targetPosition.current.set(0, 8, -12);
      targetLookAt.current.set(0, 2, 5);
    }

    // Smooth camera movement
    const lerpFactor = 3 * delta;
    
    currentPosition.current.lerp(targetPosition.current, lerpFactor);
    currentLookAt.current.lerp(targetLookAt.current, lerpFactor);

    camera.position.copy(currentPosition.current);
    camera.lookAt(currentLookAt.current);

    // Add slight camera shake when running (for immersion)
    if (gameState === 'playing') {
      const shakeIntensity = 0.02;
      const time = state.clock.elapsedTime * 10;
      
      camera.position.x += Math.sin(time * 2.1) * shakeIntensity;
      camera.position.y += Math.sin(time * 1.7) * shakeIntensity;
      
      // Slight camera sway for dynamic feel
      const sway = Math.sin(time * 0.5) * 0.1;
      camera.position.x += sway;
    }
  });

  return null;
}

export default Camera;
