import React, { useRef, useEffect, useState } from 'react';
import { useFrame } from '@react-three/fiber';
import { useBox } from '@react-three/cannon';
import * as THREE from 'three';

function Dinosaur({ gameState, gameSpeed, onCollision }) {
  const visualMeshRef = useRef(); // Visual representation
  const [isDucking, setIsDucking] = useState(false);
  const [isJumping, setIsJumping] = useState(false);
  const animationRef = useRef(0);
  const velocityRef = useRef([0, 0, 0]);
  const positionRef = useRef([0, 2, 0]);

  // Physics body for collision detection
  const [physicsRef, api] = useBox(() => ({
    mass: 1,
    position: [0, 2, 0],
    args: [1, 1, 1],
    userData: { type: 'dinosaur', isDinosaur: true },
    fixedRotation: true, // Prevent rotation
    onCollide: (e) => {
      console.log('Dinosaur collision detected!', e.body?.userData);
      if (e.body?.userData?.type === 'obstacle') {
        console.log('Hit obstacle!');
        onCollision();
      }
    }
  }));

  // Subscribe to physics body changes and sync visual mesh
  useEffect(() => {
    const unsubscribeVel = api.velocity.subscribe((velocity) => {
      velocityRef.current = velocity;
      // Update jumping state based on vertical velocity
      setIsJumping(velocity[1] > 0.1);
    });
    
    const unsubscribePos = api.position.subscribe((position) => {
      positionRef.current = position;
      // Sync visual mesh position to physics body position
      if (visualMeshRef.current) {
        visualMeshRef.current.position.set(position[0], position[1], position[2]);
      }
    });
    
    return () => {
      unsubscribeVel();
      unsubscribePos();
    };
  }, [api]);

  // Handle keyboard input
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (gameState !== 'playing') return;

      switch (event.code) {
        case 'Space':
          event.preventDefault();
          console.log('Space pressed - jumping');
          // Apply stronger jump impulse to clear even the tallest cactus (large cactus ~4.8 units high)
          api.applyImpulse([0, 15, 0], [0, 0, 0]);
          setIsJumping(true);
          break;
        case 'ArrowDown':
          if (!isJumping) {
            setIsDucking(true);
          }
          break;
        default:
          break;
      }
    };

    const handleKeyUp = (event) => {
      if (event.code === 'ArrowDown') {
        setIsDucking(false);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('keyup', handleKeyUp);
    };
  }, [gameState, isJumping, isDucking, api]);

  // Animation loop
  useFrame((state, delta) => {
    if (gameState !== 'playing' || !physicsRef.current || !visualMeshRef.current) {
      return;
    }

    animationRef.current += delta * gameSpeed * 5;

    // Force dinosaur to stay in the center of the road (X = 0, Z = 0) but allow Y movement
    if (physicsRef.current) {
      const currentPos = physicsRef.current.position;
      // Only constrain X and Z if they drift from center, don't touch Y
      if (Math.abs(currentPos.x) > 0.1 || Math.abs(currentPos.z) > 0.1) {
        api.position.set(0, currentPos.y, 0);
      }
    }

    // Log positions for debugging
    const physicsPos = physicsRef.current.position;
    const visualPos = visualMeshRef.current.position;
    console.log(
      'Physics Y:', physicsPos.y.toFixed(2), 
      'Visual Y:', visualPos.y.toFixed(2), 
      'Velocity Y:', velocityRef.current[1].toFixed(2)
    );

    // Check if we're on the ground (stop jumping state)
    if (physicsPos.y <= 1.6 && isJumping) {
      setIsJumping(false);
    }
  });

  return (
    <>
      {/* Invisible physics body for collision detection */}
      <mesh ref={physicsRef} visible={false}>
        <boxGeometry args={[1, 1, 1]} />
        <meshStandardMaterial />
      </mesh>

      {/* Visual mesh that follows the physics body */}
      <group ref={visualMeshRef} rotation={[0, 3 * Math.PI / 2, 0]}>
        <DinosaurGeometry isDucking={isDucking} isJumping={isJumping} />
      </group>
    </>
  );
}

// Custom dinosaur geometry - Enhanced T-Rex model
function DinosaurGeometry({ isDucking, isJumping }) {
  const groupRef = useRef();
  const animationRef = useRef(0);

  // Simple running animation
  useFrame((state) => {
    animationRef.current += 0.1;
  });

  return (
    <group ref={groupRef} scale={isDucking ? [1, 0.6, 1] : [1, 1, 1]}>
      {/* Main Body - More rounded */}
      <mesh position={[0, 0, 0]} castShadow receiveShadow>
        <capsuleGeometry args={[0.6, 1.2, 4, 8]} />
        <meshStandardMaterial 
          color="#7B4397" 
          roughness={0.7}
          metalness={0.1}
        />
      </mesh>

      {/* Chest - Broader front */}
      <mesh position={[0.4, 0.3, 0]} castShadow receiveShadow>
        <sphereGeometry args={[0.8, 8, 8]} />
        <meshStandardMaterial 
          color="#9B59B6" 
          roughness={0.7}
          metalness={0.1}
        />
      </mesh>

      {/* Neck */}
      <mesh position={[0.6, 0.8, 0]} castShadow receiveShadow>
        <cylinderGeometry args={[0.3, 0.4, 0.6, 8]} />
        <meshStandardMaterial 
          color="#7B4397"
          roughness={0.7}
        />
      </mesh>

      {/* Head - More detailed */}
      <mesh position={[0.9, 1.2, 0]} castShadow receiveShadow>
        <boxGeometry args={[1.0, 0.7, 0.8]} />
        <meshStandardMaterial 
          color="#7B4397"
          roughness={0.6}
        />
      </mesh>

      {/* Snout */}
      <mesh position={[1.3, 1.1, 0]} castShadow receiveShadow>
        <boxGeometry args={[0.6, 0.4, 0.6]} />
        <meshStandardMaterial 
          color="#5B2C6F"
          roughness={0.6}
        />
      </mesh>

      {/* Jaw */}
      <mesh position={[1.3, 0.9, 0]} castShadow receiveShadow>
        <boxGeometry args={[0.5, 0.2, 0.5]} />
        <meshStandardMaterial 
          color="#5B2C6F"
          roughness={0.6}
        />
      </mesh>

      {/* Eyes - Larger and more expressive */}
      <mesh position={[1.2, 1.3, 0.25]} castShadow>
        <sphereGeometry args={[0.15, 12, 12]} />
        <meshStandardMaterial color="#ffffff" />
      </mesh>
      <mesh position={[1.2, 1.3, -0.25]} castShadow>
        <sphereGeometry args={[0.15, 12, 12]} />
        <meshStandardMaterial color="#ffffff" />
      </mesh>

      {/* Pupils */}
      <mesh position={[1.28, 1.32, 0.25]} castShadow>
        <sphereGeometry args={[0.08, 12, 12]} />
        <meshStandardMaterial color="#000000" />
      </mesh>
      <mesh position={[1.28, 1.32, -0.25]} castShadow>
        <sphereGeometry args={[0.08, 12, 12]} />
        <meshStandardMaterial color="#000000" />
      </mesh>

      {/* Eye shine */}
      <mesh position={[1.32, 1.35, 0.25]} castShadow>
        <sphereGeometry args={[0.03, 8, 8]} />
        <meshStandardMaterial color="#ffffff" emissive="#ffffff" emissiveIntensity={0.3} />
      </mesh>
      <mesh position={[1.32, 1.35, -0.25]} castShadow>
        <sphereGeometry args={[0.03, 8, 8]} />
        <meshStandardMaterial color="#ffffff" emissive="#ffffff" emissiveIntensity={0.3} />
      </mesh>

      {/* Nostrils */}
      <mesh position={[1.5, 1.15, 0.15]} castShadow>
        <sphereGeometry args={[0.05, 8, 8]} />
        <meshStandardMaterial color="#4A235A" />
      </mesh>
      <mesh position={[1.5, 1.15, -0.15]} castShadow>
        <sphereGeometry args={[0.05, 8, 8]} />
        <meshStandardMaterial color="#4A235A" />
      </mesh>

      {/* Teeth */}
      <mesh position={[1.45, 1.0, 0.1]} castShadow>
        <coneGeometry args={[0.03, 0.15, 4]} />
        <meshStandardMaterial color="#f0f0f0" />
      </mesh>
      <mesh position={[1.45, 1.0, -0.1]} castShadow>
        <coneGeometry args={[0.03, 0.15, 4]} />
        <meshStandardMaterial color="#f0f0f0" />
      </mesh>
      <mesh position={[1.4, 1.0, 0.2]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#f0f0f0" />
      </mesh>
      <mesh position={[1.4, 1.0, -0.2]} castShadow>
        <coneGeometry args={[0.025, 0.12, 4]} />
        <meshStandardMaterial color="#f0f0f0" />
      </mesh>

      {/* Back Spines */}
      <mesh position={[0.2, 0.8, 0]} castShadow>
        <coneGeometry args={[0.1, 0.4, 4]} />
        <meshStandardMaterial color="#5B2C6F" />
      </mesh>
      <mesh position={[0, 0.9, 0]} castShadow>
        <coneGeometry args={[0.12, 0.5, 4]} />
        <meshStandardMaterial color="#5B2C6F" />
      </mesh>
      <mesh position={[-0.2, 0.8, 0]} castShadow>
        <coneGeometry args={[0.1, 0.4, 4]} />
        <meshStandardMaterial color="#5B2C6F" />
      </mesh>

      {/* Tail - Multiple segments for better shape */}
      <mesh position={[-0.8, 0.2, 0]} rotation={[0, 0, 0.2]} castShadow receiveShadow>
        <capsuleGeometry args={[0.3, 0.8, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>
      <mesh position={[-1.3, -0.1, 0]} rotation={[0, 0, 0.4]} castShadow receiveShadow>
        <capsuleGeometry args={[0.2, 0.6, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>
      <mesh position={[-1.7, -0.5, 0]} rotation={[0, 0, 0.6]} castShadow receiveShadow>
        <capsuleGeometry args={[0.15, 0.4, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>

      {/* Powerful Legs - Thigh */}
      <mesh position={[0.2, -0.4, 0.35]} castShadow receiveShadow>
        <capsuleGeometry args={[0.25, 0.6, 4, 8]} />
        <meshStandardMaterial color="#9B59B6" roughness={0.7} />
      </mesh>
      <mesh position={[0.2, -0.4, -0.35]} castShadow receiveShadow>
        <capsuleGeometry args={[0.25, 0.6, 4, 8]} />
        <meshStandardMaterial color="#9B59B6" roughness={0.7} />
      </mesh>

      {/* Lower Legs */}
      <mesh position={[0.3, -1.0, 0.35]} castShadow receiveShadow>
        <capsuleGeometry args={[0.18, 0.6, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>
      <mesh position={[0.3, -1.0, -0.35]} castShadow receiveShadow>
        <capsuleGeometry args={[0.18, 0.6, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>

      {/* Feet */}
      <mesh position={[0.5, -1.4, 0.35]} castShadow receiveShadow>
        <boxGeometry args={[0.5, 0.2, 0.4]} />
        <meshStandardMaterial color="#5B2C6F" roughness={0.8} />
      </mesh>
      <mesh position={[0.5, -1.4, -0.35]} castShadow receiveShadow>
        <boxGeometry args={[0.5, 0.2, 0.4]} />
        <meshStandardMaterial color="#5B2C6F" roughness={0.8} />
      </mesh>

      {/* Claws */}
      <mesh position={[0.7, -1.45, 0.35]} castShadow>
        <coneGeometry args={[0.05, 0.15, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>
      <mesh position={[0.7, -1.45, -0.35]} castShadow>
        <coneGeometry args={[0.05, 0.15, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>
      <mesh position={[0.6, -1.45, 0.45]} castShadow>
        <coneGeometry args={[0.04, 0.12, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>
      <mesh position={[0.6, -1.45, -0.45]} castShadow>
        <coneGeometry args={[0.04, 0.12, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>

      {/* Small Arms (T-Rex style) */}
      <mesh position={[0.6, 0.2, 0.5]} rotation={[0, 0, -0.3]} castShadow receiveShadow>
        <capsuleGeometry args={[0.08, 0.3, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>
      <mesh position={[0.6, 0.2, -0.5]} rotation={[0, 0, 0.3]} castShadow receiveShadow>
        <capsuleGeometry args={[0.08, 0.3, 4, 8]} />
        <meshStandardMaterial color="#7B4397" roughness={0.7} />
      </mesh>

      {/* Hand claws */}
      <mesh position={[0.7, 0.05, 0.6]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>
      <mesh position={[0.7, 0.05, -0.6]} castShadow>
        <coneGeometry args={[0.02, 0.1, 4]} />
        <meshStandardMaterial color="#2a2a2a" />
      </mesh>

      {/* Belly - Lighter colored */}
      <mesh position={[0.1, -0.3, 0]} castShadow receiveShadow>
        <sphereGeometry args={[0.7, 8, 8]} />
        <meshStandardMaterial 
          color="#BB8FCE" 
          roughness={0.6}
        />
      </mesh>
    </group>
  );
}

export default Dinosaur;
